  padding-bottom: 15px;
}
section#iuem20-portrait .personnel-info .personnel-title {
  font-style: italic;
}
section#iuem20-portrait .personnel-info .personnel-job {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;
  margin-top: 20px;
  margin-bottom: 10px;
