  font-family: 'Raleway', 'sans-serif';
  font-weight: 700;
  font-size: 1.5em;
}
section#iuem20-portrait .personnel-info .personnel-job small,
section#iuem20-portrait .personnel-info .personnel-job .small {
  font-weight: normal;
