section#iuem20-portrait .personnel-info .personnel-job .small {
  font-size: 65%;
}
section#iuem20-portrait .personnel-info .personnel-affiliation {
  padding-top: 10px;
  padding-bottom: 25px;
}
section#iuem20-portrait .personnel-img-wrapper {
  max-height: 600px;
  overflow: hidden;
}
section#iuem20-portrait .personnel-img-wrapper img {
  width: 100%;
  padding: 0px 0px 0px 0px;
}
section#iuem20-portrait .personnel-img-wrapper img .personnel-bio {
  padding: 0px 50px 0px 0px;
}
section#iuem20-portrait .personnel-bio {
  padding: 0px 0px 35px 0px;
