/* LAYOUT ---------------------------------*/
body {
  background-color: #fff;
  min-height: 100vh;
  width: auto;
  background-position: top;
  background-repeat: no-repeat;
  background-size: 100% auto;
  height: auto;
}
article#content {
  /* margin-top: @h-padding; */
  padding: 0px 0px 0px 0px;
  /* margin-right: auto;
	margin-left: auto;
	width: 1170px; */
}
article#content .iuem20-page {
  margin-left: auto;
  margin-right: auto;
  max-width: 1170px;
}
/*-----images de fond fixes-----*/
#bg-page-img {
  /*---conteneur des images---*/
  z-index: -1000;
  position: fixed;
  max-height: 60vh;
  overflow: hidden;
}
#bg-page-img img {
  width: 100vw;
}
@media (max-width: 991px) {
  /*----adaptation au format sm --*/
  div#bg-page-img img {
    height: 70vh;
    width: auto;
  }
  nav#portal-globalnav-wrapper {
    height: auto;
    padding: 0px 0px 0px 25px;
  }
  /* */
  article#content {
    padding-top: 110px;
  }
  #viewlet-above-content {
    padding: 95px 0 0 0;
  }
}
@media (max-width: 767px) {
  /*-Adaptation du margin bottom sur xs---------*/
  nav#portal-globalnav-wrapper {
    height: auto;
    padding: 0px 0px 0px 25px;
    margin: 0px 0px 0px 0px;
  }
  #iuem20-global-content {
    padding: 70px 0px 0px 0px;
  }
  /* */
  article#content {
    padding-top: 10px;
  }
  /* */
  .navbar-header button.navbar-toggle {
    margin-top: 40px;
  }
  .navbar-brand img {
    height: 30px;
  }
}
/*----bloc de titre-*/
#page-title {
  background-color: #fff;
  padding: 15px 15px 25px 15px;
  margin-bottom: 150px;
  border-radius: 0px 0px 10px 10px;
}
@media (max-width: 767px) {
  /*-Adaptation du margin bottom sur xs---------*/
  div#page-title {
    margin-bottom: 15px;
  }
}
