.custom-theme-marker {
  display: none;
  position: absolute;
  top: 300px;
  left: 30%;
  margin: auto 0;
  transform: rotate(-20deg);
  background: #00ECFF;
  color: #fff;
  padding: 1em;
  z-index: 100;
}
.section-front-page .custom-theme-marker {
  display: block;
}
/* TinyMCE templates */
.image-grid-2x2 {
  padding: 0;
  margin: 0 0 30px 0;
  list-style: none;
  margin-left: -15px;
  margin-right: -15px;
}
.image-grid-2x2 > li {
  float: left;
  width: 100%;
  position: relative;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 30px;
}
@media (min-width: 768px) {
  .image-grid-2x2 > li {
    float: left;
    width: 50%;
  }
}
@media (min-width: 768px) {
  .image-grid-2x2 > li {
    float: left;
